.banner {
  padding: 50px 5%;
  height: 60vh;
  padding-top: 35vh;
  // background: cornflowerblue;
  background: url(../../imgs/header-bg.jpg);
  background-position: center;
  background-size: cover;
  color: #fff;
  h2,
  p {
    margin-bottom: 16px;
  }
  .place-search,
  .date-search,
  .info-search {
    display: flex;
    // border: 5px solid #febb02;
    // border-right: 0px;
    color: black;
  }
  .info-search,.place-search {
    border: 5px solid #febb02;

  }
  
}
.date-search{
  border-top: 5px solid #febb02;
  border-bottom: 5px solid #febb02;
}
form {
  display: grid;
  grid-template-columns: 2fr 2fr 3fr;
  margin-bottom: 16px;
}

.search-item {
  background: #fff;
  padding: 5px 10px;
  width: 100%;
  input {
    display: inherit;
    height: 15px;
    padding: 5px;

    width: 85%;
  }
  input {
    margin-top: 10px;
  }
}

@media screen and (max-width: 480px) {
  form {
    grid-template-columns: 1fr;
  }
  .place-search,
  .date-search,
  .info-search {
    display: flex;
    // border: 5px solid #febb02;
  }
  .banner{
    padding-top: 15vh;
  }
  .date-search{
    border: 0px;
    border-left: 5px solid #febb02;
    border-right: 5px solid #febb02;
  }

}
